.displayLeft {
  height: 50%;
  width: 100%;
  margin-top: 18%;
  font-family: 'Lobster', cursive;
  font-size: 500%;
  position: absolute;
  z-index: 500;
  display: flex;
  text-align: left;
  justify-content: flex-start;
  align-content: left;
  padding: 5px;
  opacity:0;
  animation: shimmy 2s;
}

@keyframes shimmy {
  0% {
    opacity: 0;
    transform: translate(0, 0);
  }
  100% {
   opacity:1;
   transform: translate(35%, 0);
  }
}

.displayRight {
  height: 50%;
  width: 100%;
  margin-top: 28%;
  font-family: 'Lobster', cursive;
  font-size: 500%;
  display: flex;
  text-align: right;
  justify-content: flex-end;
  align-content: right;
  padding: 5px;
  position: absolute;
  z-index: 500;
  opacity:0;
  animation: shimmyRight 2s;
}

@keyframes shimmyRight {
  0% {
    opacity: 0;
    transform: translate(0, 0);
  }
  100% {
   opacity:1;
   transform: translate(-35%, 0);
  }
}

